:root {
    --rapture-tower-box-shadow: 
        600px -550px 250px 1px #092d3f, 
        -600px -550px 250px 1px #092d3f, 
        -150px -150px 250px 10px var(--rapture-sea-green), 
        150px -150px 250px 10px var(--rapture-sea-green), 
        -200px 150px 100px 5px var(--rapture-fluorescent), 
        200px 150px 100px 5px var(--rapture-fluorescent), 
        250px 250px 200px 10px var(--white), 
        -250px 250px 200px 10px var(--white);
    --beacon-shadow: 
        0px 0px 50px 10px var(--rapture-fluorescent), 
        0px 0px 100px 10px var(--rapture-fluorescent), 
        0px 0px 25px var(--rapture-fluorescent);

    --javascript-logo-scheme: #F0DB4F;
    --reactjs-logo-scheme: radial-gradient(circle, #61DBFB 10%, #333333 10%) no-repeat center center;
    --python-logo-scheme: whitesmoke;
    --css-logo-scheme: #2965f1;
    --html-logo-scheme: #f06529;
    --ruby-logo-scheme:#DE3F24;
    --c-logo-scheme: #283593;
    --c-sharp-logo-scheme: #682676;
    --golang-logo-scheme: #29BEB0;
    --git-logo-scheme: #F1502F;
    --nodejs-logo-scheme: #3c873a;
    --ocaml-logo-scheme:#ec6a12;
    --rust-logo-scheme: black;
    --java-logo-scheme: whitesmoke;
    --java-blue: #007396;
    --java-orange: #ED8B00;
}

.pf-bot-section {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
}
@keyframes lighthouse-build {
    0% {
        background: transparent;
        height: 0vh;
    }
    100% {
        background: var(--rapture-sea-green);
        height: 100vh;
    }
}

.rapture-container {
    height: 40vh;
    position: relative;
    width: 100vw;
}
.rapture-main {
    width: 10vh;
    height: 80vh;
    position: absolute;
    background: var(--rapture-sea-green);
    left: calc(50% - 5vh);
    bottom: 0;
    animation: rapture-main-build 3s ease;
}
.lighthouse-beacon {
    cursor: pointer;
    width: 50%;
    margin-left: 25%;
    position: absolute;
    background: white;
    padding-bottom: 50%;
    margin-top: calc(-20vh + 10px);
    border-radius: 50%;
}
.lighthouse-beacon:hover{
    box-shadow: var(--beacon-shadow);
}
@keyframes rapture-main-build {
    0% {
        bottom: -150%;
    }
    100% {
        bottom: 0;
    }
}

.tower {
    position: absolute;
    bottom: 0;
    box-shadow: var(--rapture-tower-box-shadow);
    animation-name: rise-up;
    animation-timing-function: ease;
}
.tower-stacks {
    position: absolute;
}
@keyframes rise-up {
    0% {
        transform: translateY(150%);
        opacity: 0;
    }
    100% {
        transform: translateY(0%);
        opacity: 1;
    }
}

.bubbles-container {
    margin-top: 60vh;
}
.bubble {
    border-radius: 100%;
    position: absolute;
    box-shadow: 0 0 20px white, inset 0 0 5px white;
    opacity: 0;
    animation: bubble 15s ease-in-out infinite, sideWays 4s ease-in-out infinite alternate;
}
@keyframes bubble {
    0% {
        transform: translateY(30vh);
        opacity: 0.3;
    }
    90% {
        opacity: 1;
    }
    100% {
        opacity: 0.1;
        transform: translateY(-65vh);
    }
}
@keyframes sideWays {
    0% {
        margin-left: 0px;
    }
    100% {
        margin-left: 20%;
    }
}

.fishes-container {
    position: absolute;
    width: 100vw;
    height: 100vh;
    bottom: -100vh;
}
.fish-school {
    position: absolute;
    left: -2%;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}
.fish {
    width: 1vw;
    height: 1vh;
    border-radius: 50%;
    background: white;
    position: absolute;
}
.fish::after {
    content: '';
    background: white;
    width: 45%;
    height: 150%;
    position: absolute;
    right: 35%;
    top: -25%;
    border-radius: 0% 100% 75% 25% / 0% 50% 50% 100% ;
}
.fish::before {
    content: '';
    background: white;
    width: 25%;
    height: 100%;
    position: absolute;
    left: -15%;
    border-radius: 0% 100% 100% 0% / 0% 50% 50% 100%;
}
  
@keyframes swim {
    0% { margin-left: -5%; }
    100% { margin-left: 105%; }
}

@keyframes swim-reverse {
    0% { margin-left: 105%; }
    100% { margin-left: -5%; }
}

.bathysphere {
    width: 50%;
    padding-bottom: 50%;
    position: relative;
    left: 25%;
    margin-top: 25%;
    border-radius: 15%;
    box-shadow: 0px 0px 5px 3px white, inset 0px 0px 5px 3px white;
    animation: bathy-glow 1s ease alternate infinite;
    cursor: pointer;
}
.bathysphere:hover {
    box-shadow: 0px 0px 50px 10px white, inset 0px 0px 50px 3px white;
}
.fathomless-depths {
    width: 10vw;
    height: 25vh;
    position: absolute;
    bottom: -5%;
    z-index: 6;
    opacity: 0.5;
    left: calc(50% - 5vw);
    background: white;
    background: linear-gradient(180deg, transparent 0%, white 100%);
    cursor: pointer;
}
.fathomless-depths:hover {
    opacity: 1;
}

.panels-container {
    height: 50vh;
    margin-top: 10vh;
    margin-left: 10px;
    display: flex;
    z-index: 12;
    position: absolute;
    font-family: market-deco;
    color: white;
    text-shadow: 0 0 0px white;
    flex-direction: column;
    filter: drop-shadow(2px 4px 6px black);
    user-select: none;
}
.panels-container > div {   
    margin: 10px;
}
.content-panel {
    position: relative;
    cursor: pointer;
    opacity: 0.7;
}
.content-panel:hover {
    opacity: 1;
}
.content-title {
    font-size: 2em;
}
.contents-container {
    position: relative;
}
.contents-container > .content {
    font-size: 1em;
}
.skills-container {
    display: flex;
}
.personal-skill-icon {
    width: 3em;
    height: 3em;
    margin: 5px;
    position: relative;
    z-index: 2;
}
.personal-skill-icon::-webkit-scrollbar {
    display: none;
}
.personal-skill-icon:hover > .proficiency-text {
    bottom: -42%;
    animation: slow-show 0.3s ease;
    overflow: unset;
}
.personal-skill-icon > .proficiency-text {
    height: 20%;
    position: absolute;
    width: 100%;
    bottom: 50%;
    z-index: 1;
    text-align: center;
    overflow: hidden;
}
.personal-skill-icon > .skill {
    width: 100%;
    height: 100%;
    border-radius: 5%;
    position: relative;
    z-index: 2;
}
@keyframes slow-show {
    from {
        bottom: 50%;
    }
    to {
        bottom: -42%;
    }
}

